@font-face {
    font-family: 'Novecento Sans Wide';
    src: url('../fonts/Light/Novecentosanswide-Light-webfont.eot');
    src: url('../fonts/Light/Novecentosanswide-Light-webfont.eot?#iefix') format('embedded-opentype'), 
        url('../fonts/Light/Novecentosanswide-Light-webfont.woff2') format('woff2'), 
        url('../fonts/Light/Novecentosanswide-Light-webfont.woff') format('woff'), 
        url('../fonts/Light/Novecentosanswide-Light-webfont.ttf') format('truetype'),
        url('../fonts/Light/Novecentosanswide-Light-webfont.svg#novecento_sans_widelight') format('svg');
    font-weight: 200;
    font-style: normal;
}

